<script>
import PersonView from "../components/PersonViewComponent.vue";
import ClerkView from "../components/ClerkViewComponent.vue";
import CustomerView from "../components/CustomerViewComponent.vue";
import VehicleView from "../components/VehicleViewComponent.vue";
import InstructionsView from "../components/TwoInstructionsViewComponent.vue";
import ReservationSystemView from "../components/RentalSystemViewComponent.vue";
import ClassView from "../components/ClassDiagramViewComponent.vue";
import RentalSystem from '../components/RentalSystemComponent.vue';
import TestDriverViewComponent from '../components/TestDriverViewComponent.vue';
//rental
//test
export default {
  components: {
    CustomerView,
    VehicleView,
    InstructionsView,
    ReservationSystemView,
    ClassView,
    PersonView,
    ClerkView,
    RentalSystem,
    TestDriverViewComponent
  },
  data() {
    return {
      componentSelect: "InstructionsView",
      testdriver_completed: false,
      rental_system_completed: false
    };
  },
  methods: {
    testDriverCallBack(value){
      console.log("Test driver callback triggered", value);
      this.testdriver_completed = true;
    },
    rentalSystemCallBack(value){
      console.log("Rental system callback triggered", value);
      this.rental_system_completed = true;
    },
    instructionsReadCallback(value) {
      console.log("Instructions callback triggered", value);
      this.componentSelect = 'RentalSystem';
    }
  },
};
</script>

<template>
  <main class="app">
    <button @click="componentSelect = 'InstructionsView'" :style="componentSelect === 'InstructionsView' ? 'background-color: #55acee' : ''">1. Instructions</button>
    <button @click="componentSelect = 'PersonView'" :style="componentSelect === 'PersonView' ? 'background-color: #55acee' : ''">2. Person Class</button>
    <button @click="componentSelect = 'CustomerView'" :style="componentSelect === 'CustomerView' ? 'background-color: #55acee' : ''">3. Customer Class</button>
    <button @click="componentSelect = 'ClerkView'" :style="componentSelect === 'ClerkView' ? 'background-color: #55acee' : ''">4. Clerk Class</button>
    <button @click="componentSelect = 'VehicleView'" :style="componentSelect === 'VehicleView' ? 'background-color: #55acee' : ''">5. Vehicle Class</button>
    <button @click="componentSelect = 'RentalSystem'" :style="componentSelect === 'RentalSystem' ? 'background-color: #55acee' : ''">6. Rental System Class</button>
    <button @click="componentSelect = 'TestDriverViewComponent'" :style="componentSelect === 'TestDriverViewComponent' ? 'background-color: #55acee' : ''">7. Test Driver</button>
    <button @click="componentSelect = 'ClassView'" :style="componentSelect === 'ClassView' ? 'background-color: #55acee' : ''">8. Class diagram</button>
    <KeepAlive>
      <component
        v-bind:is="componentSelect"
        @testdriver-complete="testDriverCallBack"
        @rental-system-complete="rentalSystemCallBack"
        @instructions-read="instructionsReadCallback"
      />
    </KeepAlive>
  </main>
  <footer v-show="(rental_system_completed)">
    <div id="buttons" class="relative">
      <button class="navitem" @click="$router.push('/method-completion-execution')">Next</button>
    </div>
  </footer>
</template>

<style>
.app {
  background-color: #f9fafe;
  min-height: 1024px;
}

button {
  margin-top: 30px;
  border: none;
  padding: 20px;
  margin-left: 5px;
  background-color: #2c3e50;
  color: white;
}

button:first-of-type {
  margin-left: 30px;
}
</style>
